"use client";

import { useState } from "react";
import {
  DollarSign,
  ArrowUpRight,
  ArrowDownLeft,
  CreditCard,
  Clock,
  Check,
  X,
  Filter,
  ChevronDown,
} from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

// Import wallet deposit components
import {
  PaymentMethodsGrid,
  DepositDrawer,
  DepositAmountStep,
  DepositConfirmStep,
  CryptoNetworkStep,
  CryptoAddressStep,
  WireTransferStep,
  TransactionRow,
  PendingDepositsSection,
  useDepositFlow,
  WalletTransaction,
  PendingDeposit,
} from "./wallet";

// Demo wallet data
const mockWallet = {
  balance: 850.0,
  totalSpent: 2450.0,
  pendingBalance: 125.0,
  personalBalance: 500.0,
};

// Demo transactions using proper WalletTransaction type
const mockTransactions: WalletTransaction[] = [
  {
    id: "1",
    type: "deposit",
    amount: 500,
    status: "completed",
    description: "Card deposit",
    created_at: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000).toISOString(),
  },
  {
    id: "2",
    type: "crypto_deposit",
    amount: 1000,
    status: "completed",
    description: "USDC on Solana",
    created_at: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000).toISOString(),
    metadata: {
      network: "solana",
      tx_signature: "5abc123...",
    },
  },
  {
    id: "3",
    type: "payout",
    amount: -200,
    status: "completed",
    description: "Service payment",
    created_at: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString(),
  },
  {
    id: "4",
    type: "withdrawal",
    amount: -300,
    status: "pending",
    description: "Withdrawal to bank",
    created_at: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000).toISOString(),
  },
  {
    id: "5",
    type: "refund",
    amount: 75,
    status: "completed",
    description: "Session cancelled",
    created_at: new Date(Date.now() - 10 * 24 * 60 * 60 * 1000).toISOString(),
  },
];

// Demo pending deposits
const mockPendingDeposits: PendingDeposit[] = [
  // Empty for now - uncomment to test:
  // {
  //   id: "p1",
  //   type: "crypto_deposit",
  //   amount: 250,
  //   status: "pending",
  //   created_at: new Date(Date.now() - 30 * 60 * 1000).toISOString(),
  //   network: "solana",
  // },
];

// Demo crypto addresses
const cryptoAddresses: Record<string, string> = {
  solana: "9WzDXwBbmkg8ZTbNMqUxvQRAyrZzDsGYdLVL9zYtAWWM",
  base: "0x742d35Cc6634C0532925a3b844Bc9e7595f3b21B",
  ethereum: "0x742d35Cc6634C0532925a3b844Bc9e7595f3b21B",
  polygon: "0x742d35Cc6634C0532925a3b844Bc9e7595f3b21B",
  arbitrum: "0x742d35Cc6634C0532925a3b844Bc9e7595f3b21B",
  optimism: "0x742d35Cc6634C0532925a3b844Bc9e7595f3b21B",
};

// Demo wire details
const wireDetails = {
  accountName: "Waliet Inc.",
  accountNumber: "1234567890",
  routingNumber: "021000021",
  bankName: "JPMorgan Chase Bank",
  bankAddress: "383 Madison Ave, New York, NY 10179",
  reference: "WAL-USER-12345",
};

export function WalletTab() {
  const [wallet] = useState(mockWallet);
  const [transactions] = useState(mockTransactions);
  const [pendingDeposits] = useState(mockPendingDeposits);
  const [filterType, setFilterType] = useState("all");

  // Deposit flow state
  const depositFlow = useDepositFlow();

  const filteredTransactions = transactions.filter((tx) => {
    if (filterType === "all") return true;
    return tx.type === filterType;
  });

  // Handle deposit confirmation
  const handleConfirmDeposit = async () => {
    depositFlow.setLoading(true);
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 1500));
    depositFlow.success();
    // In real app, refresh wallet data here
    setTimeout(() => {
      depositFlow.reset();
    }, 2000);
  };

  // Handle network selection and move to address step
  const handleNetworkSelect = async (network: typeof depositFlow.network) => {
    if (!network) return;
    depositFlow.setNetwork(network);
    depositFlow.setLoading(true);
    // Simulate fetching address
    await new Promise((resolve) => setTimeout(resolve, 500));
    depositFlow.setLoading(false);
    depositFlow.nextStep();
  };

  // Render current deposit step content
  const renderDepositContent = () => {
    switch (depositFlow.step) {
      case "network":
        return (
          <CryptoNetworkStep
            selectedNetwork={depositFlow.network}
            onSelectNetwork={handleNetworkSelect}
            isLoading={depositFlow.isLoading}
          />
        );

      case "amount":
        return (
          <DepositAmountStep
            method={depositFlow.method!}
            amount={depositFlow.amount}
            onAmountChange={depositFlow.setAmount}
            personalBalance={wallet.personalBalance}
            onContinue={depositFlow.nextStep}
            isLoading={depositFlow.isLoading}
          />
        );

      case "confirm":
        return depositFlow.fees ? (
          <DepositConfirmStep
            method={depositFlow.method!}
            amount={depositFlow.amount}
            fees={depositFlow.fees}
            isLoading={depositFlow.isLoading}
            onConfirm={handleConfirmDeposit}
            onCancel={depositFlow.reset}
          />
        ) : null;

      case "address":
        if (depositFlow.method === "crypto" && depositFlow.network) {
          return (
            <CryptoAddressStep
              network={depositFlow.network}
              address={cryptoAddresses[depositFlow.network] || ""}
            />
          );
        }
        if (depositFlow.method === "wire") {
          return (
            <WireTransferStep
              accountName={wireDetails.accountName}
              accountNumber={wireDetails.accountNumber}
              routingNumber={wireDetails.routingNumber}
              bankName={wireDetails.bankName}
              bankAddress={wireDetails.bankAddress}
              reference={wireDetails.reference}
            />
          );
        }
        return null;

      case "success":
        return (
          <div className="text-center py-8">
            <div className="w-16 h-16 rounded-full bg-green-500/10 flex items-center justify-center mx-auto mb-4">
              <Check className="h-8 w-8 text-green-600" />
            </div>
            <h3 className="text-lg font-semibold mb-2">Deposit Initiated</h3>
            <p className="text-sm text-muted-foreground">
              Your deposit is being processed.
            </p>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="space-y-6 max-w-4xl">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">Wallet</h1>
          <p className="text-muted-foreground text-sm mt-1">
            Manage your funds and transactions
          </p>
        </div>
        <div className="flex gap-2">
          <Button variant="outline" className="gap-2">
            <ArrowUpRight className="w-4 h-4" />
            Withdraw
          </Button>
        </div>
      </div>

      {/* Balance Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card className="bg-card border-border">
          <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
              <p className="text-sm text-muted-foreground font-medium">
                Available Balance
              </p>
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <DollarSign className="w-5 h-5 text-primary" />
              </div>
            </div>
            <p className="text-3xl font-bold tracking-tight">
              ${wallet.balance.toLocaleString()}
            </p>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
              <p className="text-sm text-muted-foreground font-medium">
                Total Spent
              </p>
              <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center">
                <ArrowUpRight className="w-5 h-5 text-primary" />
              </div>
            </div>
            <p className="text-3xl font-bold tracking-tight">
              ${wallet.totalSpent.toLocaleString()}
            </p>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-6">
            <div className="flex items-center justify-between mb-4">
              <p className="text-sm text-muted-foreground font-medium">
                Pending
              </p>
              <div className="w-10 h-10 rounded-lg bg-warning/10 flex items-center justify-center">
                <Clock className="w-5 h-5 text-warning" />
              </div>
            </div>
            <p className="text-3xl font-bold tracking-tight">
              ${wallet.pendingBalance.toLocaleString()}
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Pending Deposits */}
      <PendingDepositsSection deposits={pendingDeposits} />

      {/* Payment Methods Grid - Deposit Options */}
      <Card className="bg-card border-border">
        <CardContent className="p-6">
          <PaymentMethodsGrid
            onSelectMethod={depositFlow.selectMethod}
            personalBalance={wallet.personalBalance}
          />
        </CardContent>
      </Card>

      {/* Transactions */}
      <Card className="bg-card border-border">
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between">
            <CardTitle className="text-base">Transaction History</CardTitle>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="outline" size="sm" className="gap-2">
                  <Filter className="w-3 h-3" />
                  {filterType === "all" ? "All" : filterType}
                  <ChevronDown className="w-3 h-3" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuItem onClick={() => setFilterType("all")}>
                  All
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setFilterType("deposit")}>
                  Deposits
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setFilterType("crypto_deposit")}>
                  Crypto Deposits
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setFilterType("payout")}>
                  Payouts
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setFilterType("withdrawal")}>
                  Withdrawals
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setFilterType("refund")}>
                  Refunds
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </CardHeader>
        <CardContent className="pt-0">
          <div className="divide-y divide-border">
            {filteredTransactions.map((tx) => (
              <TransactionRow key={tx.id} transaction={tx} />
            ))}
          </div>

          {filteredTransactions.length === 0 && (
            <div className="text-center py-8">
              <p className="text-muted-foreground text-sm">
                No transactions found
              </p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Deposit Drawer */}
      <DepositDrawer
        state={depositFlow}
        isOpen={depositFlow.isOpen}
        onClose={depositFlow.reset}
      >
        {renderDepositContent()}
      </DepositDrawer>
    </div>
  );
}
