"use client";

import { useState } from "react";
import {
  Edit2,
  Star,
  Calendar,
  DollarSign,
  Clock,
  MapPin,
  Link as LinkIcon,
  Twitter,
  Linkedin,
  X,
  Camera,
} from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";

const initialProfile = {
  name: "Demo User",
  username: "@demouser",
  avatar: null as string | null,
  banner: null,
  bio: "Passionate about building great products and helping others succeed. Always learning, always growing.",
  location: "San Francisco, CA",
  website: "https://example.com",
  twitter: "@demouser",
  linkedin: "demouser",
  memberSince: "January 2024",
  stats: {
    sessionsCompleted: 12,
    totalSpent: 1850,
    averageRating: 4.9,
    hoursBooked: 18,
  },
  recentSessions: [
    {
      id: "1",
      seller: { name: "Alex Chen", avatar: null },
      title: "Business Strategy",
      date: "Jan 20, 2024",
      rating: 5,
    },
    {
      id: "2",
      seller: { name: "Sarah Johnson", avatar: null },
      title: "Marketing Review",
      date: "Jan 15, 2024",
      rating: 5,
    },
    {
      id: "3",
      seller: { name: "Michael Brown", avatar: null },
      title: "Design Feedback",
      date: "Jan 10, 2024",
      rating: 4,
    },
  ],
  badges: [
    { id: "1", name: "Early Adopter", icon: "ðŸš€" },
    { id: "2", name: "Power Buyer", icon: "âš¡" },
    { id: "3", name: "Top Reviewer", icon: "â­" },
  ],
};

export function ProfileTab() {
  const [profile, setProfile] = useState(initialProfile);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const [editForm, setEditForm] = useState({
    name: profile.name,
    username: profile.username,
    bio: profile.bio,
    location: profile.location,
    website: profile.website,
    twitter: profile.twitter,
    linkedin: profile.linkedin,
  });
  const [isSaving, setIsSaving] = useState(false);

  const handleOpenEditDialog = () => {
    setEditForm({
      name: profile.name,
      username: profile.username,
      bio: profile.bio,
      location: profile.location,
      website: profile.website,
      twitter: profile.twitter,
      linkedin: profile.linkedin,
    });
    setIsEditDialogOpen(true);
  };

  const handleSaveProfile = async () => {
    setIsSaving(true);
    // Simulate API call
    await new Promise((resolve) => setTimeout(resolve, 500));

    setProfile((prev) => ({
      ...prev,
      name: editForm.name,
      username: editForm.username,
      bio: editForm.bio,
      location: editForm.location,
      website: editForm.website,
      twitter: editForm.twitter,
      linkedin: editForm.linkedin,
    }));

    setIsSaving(false);
    setIsEditDialogOpen(false);
  };

  return (
    <div className="space-y-6 max-w-4xl">
      {/* Profile Header */}
      <Card className="bg-card border-border overflow-hidden">
        {/* Banner */}
        <div className="h-32 bg-gradient-to-r from-primary/20 via-primary/10 to-primary/5" />

        <CardContent className="relative pt-0 pb-6">
          {/* Avatar */}
          <div className="absolute -top-12 left-6">
            <Avatar className="w-24 h-24 border-4 border-background">
              <AvatarImage src={profile.avatar || undefined} />
              <AvatarFallback className="bg-muted text-muted-foreground text-2xl">
                {profile.name[0]}
              </AvatarFallback>
            </Avatar>
          </div>

          {/* Edit Button */}
          <div className="flex justify-end pt-2">
            <Button
              variant="outline"
              size="sm"
              className="gap-2"
              onClick={handleOpenEditDialog}
            >
              <Edit2 className="w-3 h-3" />
              Edit Profile
            </Button>
          </div>

          {/* Profile Info */}
          <div className="mt-4 space-y-3">
            <div>
              <h1 className="text-xl font-bold tracking-tight">{profile.name}</h1>
              <p className="text-sm text-muted-foreground">{profile.username}</p>
            </div>

            <p className="text-sm text-muted-foreground">{profile.bio}</p>

            <div className="flex flex-wrap gap-4 text-xs text-muted-foreground">
              {profile.location && (
                <span className="flex items-center gap-1">
                  <MapPin className="w-3 h-3" />
                  {profile.location}
                </span>
              )}
              {profile.website && (
                <a
                  href={profile.website}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex items-center gap-1 text-primary hover:underline"
                >
                  <LinkIcon className="w-3 h-3" />
                  {profile.website.replace("https://", "")}
                </a>
              )}
              <span className="flex items-center gap-1">
                <Calendar className="w-3 h-3" />
                Joined {profile.memberSince}
              </span>
            </div>

            {/* Social Links */}
            <div className="flex gap-2">
              {profile.twitter && (
                <a
                  href={`https://twitter.com/${profile.twitter.replace("@", "")}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="p-2 rounded-lg bg-muted hover:bg-muted/80 transition-colors"
                >
                  <Twitter className="w-4 h-4 text-muted-foreground" />
                </a>
              )}
              {profile.linkedin && (
                <a
                  href={`https://linkedin.com/in/${profile.linkedin}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="p-2 rounded-lg bg-muted hover:bg-muted/80 transition-colors"
                >
                  <Linkedin className="w-4 h-4 text-muted-foreground" />
                </a>
              )}
            </div>

            {/* Badges */}
            <div className="flex flex-wrap gap-2 pt-2">
              {profile.badges.map((badge) => (
                <Badge key={badge.id} variant="secondary" className="gap-1">
                  <span>{badge.icon}</span>
                  {badge.name}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
        <Card className="bg-card border-border">
          <CardContent className="p-4 text-center">
            <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
              <Calendar className="w-5 h-5 text-primary" />
            </div>
            <p className="text-2xl font-bold tracking-tight">{profile.stats.sessionsCompleted}</p>
            <p className="text-xs text-muted-foreground">Sessions</p>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4 text-center">
            <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
              <DollarSign className="w-5 h-5 text-primary" />
            </div>
            <p className="text-2xl font-bold tracking-tight">
              ${profile.stats.totalSpent.toLocaleString()}
            </p>
            <p className="text-xs text-muted-foreground">Total Spent</p>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4 text-center">
            <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
              <Star className="w-5 h-5 text-primary" />
            </div>
            <p className="text-2xl font-bold tracking-tight">{profile.stats.averageRating}</p>
            <p className="text-xs text-muted-foreground">Avg Rating Given</p>
          </CardContent>
        </Card>

        <Card className="bg-card border-border">
          <CardContent className="p-4 text-center">
            <div className="w-10 h-10 rounded-lg bg-primary/10 flex items-center justify-center mx-auto mb-2">
              <Clock className="w-5 h-5 text-primary" />
            </div>
            <p className="text-2xl font-bold tracking-tight">{profile.stats.hoursBooked}</p>
            <p className="text-xs text-muted-foreground">Hours Booked</p>
          </CardContent>
        </Card>
      </div>

      {/* Recent Sessions */}
      <Card className="bg-card border-border">
        <CardHeader className="pb-3">
          <CardTitle className="text-base">Recent Sessions</CardTitle>
        </CardHeader>
        <CardContent className="pt-0">
          <div className="divide-y divide-border">
            {profile.recentSessions.map((session) => (
              <div key={session.id} className="flex items-center gap-3 py-3">
                <Avatar className="w-10 h-10">
                  <AvatarImage src={session.seller.avatar || undefined} />
                  <AvatarFallback className="bg-muted text-muted-foreground text-xs">
                    {session.seller.name[0]}
                  </AvatarFallback>
                </Avatar>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-medium truncate">{session.title}</p>
                  <p className="text-xs text-muted-foreground">with {session.seller.name}</p>
                </div>
                <div className="text-right">
                  <div className="flex items-center gap-1">
                    <Star className="w-3 h-3 text-primary fill-primary" />
                    <span className="text-xs font-medium">{session.rating}</span>
                  </div>
                  <p className="text-[10px] text-muted-foreground">{session.date}</p>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Edit Profile Dialog */}
      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent className="sm:max-w-[500px] bg-background border-border">
          <DialogHeader>
            <DialogTitle>Edit Profile</DialogTitle>
          </DialogHeader>

          <div className="space-y-4 py-4">
            {/* Avatar Upload */}
            <div className="flex items-center gap-4">
              <div className="relative">
                <Avatar className="w-16 h-16">
                  <AvatarImage src={profile.avatar || undefined} />
                  <AvatarFallback className="bg-muted text-muted-foreground text-xl">
                    {editForm.name[0] || "U"}
                  </AvatarFallback>
                </Avatar>
                <button className="absolute -bottom-1 -right-1 w-6 h-6 rounded-full bg-primary flex items-center justify-center text-primary-foreground hover:bg-primary/90 transition-colors">
                  <Camera className="w-3 h-3" />
                </button>
              </div>
              <div className="flex-1">
                <p className="text-sm font-medium">Profile Photo</p>
                <p className="text-xs text-muted-foreground">Click the camera icon to upload a new photo</p>
              </div>
            </div>

            {/* Name */}
            <div className="space-y-2">
              <Label htmlFor="name">Name</Label>
              <Input
                id="name"
                value={editForm.name}
                onChange={(e) => setEditForm((prev) => ({ ...prev, name: e.target.value }))}
                placeholder="Your name"
              />
            </div>

            {/* Username */}
            <div className="space-y-2">
              <Label htmlFor="username">Username</Label>
              <Input
                id="username"
                value={editForm.username}
                onChange={(e) => setEditForm((prev) => ({ ...prev, username: e.target.value }))}
                placeholder="@username"
              />
            </div>

            {/* Bio */}
            <div className="space-y-2">
              <Label htmlFor="bio">Bio</Label>
              <Textarea
                id="bio"
                value={editForm.bio}
                onChange={(e) => setEditForm((prev) => ({ ...prev, bio: e.target.value }))}
                placeholder="Tell us about yourself"
                rows={3}
              />
            </div>

            {/* Location */}
            <div className="space-y-2">
              <Label htmlFor="location">Location</Label>
              <Input
                id="location"
                value={editForm.location}
                onChange={(e) => setEditForm((prev) => ({ ...prev, location: e.target.value }))}
                placeholder="City, Country"
              />
            </div>

            {/* Website */}
            <div className="space-y-2">
              <Label htmlFor="website">Website</Label>
              <Input
                id="website"
                value={editForm.website}
                onChange={(e) => setEditForm((prev) => ({ ...prev, website: e.target.value }))}
                placeholder="https://example.com"
              />
            </div>

            {/* Social Links */}
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="twitter">Twitter</Label>
                <Input
                  id="twitter"
                  value={editForm.twitter}
                  onChange={(e) => setEditForm((prev) => ({ ...prev, twitter: e.target.value }))}
                  placeholder="@username"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="linkedin">LinkedIn</Label>
                <Input
                  id="linkedin"
                  value={editForm.linkedin}
                  onChange={(e) => setEditForm((prev) => ({ ...prev, linkedin: e.target.value }))}
                  placeholder="username"
                />
              </div>
            </div>
          </div>

          <DialogFooter>
            <Button
              variant="outline"
              onClick={() => setIsEditDialogOpen(false)}
              disabled={isSaving}
            >
              Cancel
            </Button>
            <Button onClick={handleSaveProfile} disabled={isSaving}>
              {isSaving ? "Saving..." : "Save Changes"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
